import React from "react";
import "./HowWeWork.scss";

export default function HowWeWork() {
  const howWeWorkArray = [
    { name: "  ", img: "/img/builder_1.png" },
    { name: "   ", img: "/img/consent_1.png" },
    { name: "", img: "/img/official-documents_1.png" },
    { name: " ", img: "/img/gdpr_1.png" },
    {
      name: ":   ",
      img: "/img/cctv_1.png"
    },
    { name: "  ", img: "/img/contract_1.png" },
    { name: "    ", img: "/img/cooperation_1.png" },
    { name: "   ", img: "/img/guarantee_1.png" }
  ];

  return <section className="HowWeWork container">
      <h2 className="anim_item">  </h2>
      <p className="anim_item">
           :    ,  
        : , , .     .
      </p>
      <div className="box_card_work">
        {howWeWorkArray.map(el => <div className="card_work">
            <div>
              <div className="box_img">
                <img src={el.img} alt="#" />
              </div>
              <h4>
                {el.name}
              </h4>
            </div>
            <div>
              <div className="line" />
              <div className="circle" />
            </div>
          </div>)}
      </div>
    </section>;
}
