import React, { useRef, useState } from "react";
import { useForm } from "react-hook-form";
import { IMaskInput } from "react-imask";
import { Button } from "../Button/Button";
import "./FormontactUs.scss";

export default function FormontactUs({statusComment}) {
  const inputRef = useRef(null);
  const [phoneNumber, setPhoneNumber] = useState("");
  const [isInputValid, setInputValid] = useState(false);
    const { setValue, register, handleSubmit, reset, formState: { errors } } = useForm();
    const validateName = /^[-][-]*$/ 

  const onSubmit = data => {
    if (isInputValid) {
      inputRef.current.classList.remove("error");
      console.log(data);
      setPhoneNumber("");
      inputRef.current.value = "";
    } else {
      inputRef.current.classList.add("error");
    }
    reset();
  };

  const handleAccept = () => {
    setInputValid(false);
    setPhoneNumber(inputRef.current.value);
    setValue("phone", inputRef.current.value);
  };

  const handleEmpty = () => {
    setInputValid(true);
  };

  return (
      <form className="FormontactUs" onSubmit={handleSubmit(onSubmit)}>
       <label className='box_input'>
          ?
        <input className={errors?.name && 'error'} {...register("name", { required: true, pattern: validateName  })} placeholder='' />
          </label>
          <label className="box_input">
                
        <IMaskInput
          mask="+{38}(000)-00-00-000"
          radix="."
          unmask={false}
          inputRef={inputRef}
          value={phoneNumber}
          onAccept={handleAccept}
          onComplete={handleEmpty}
          className={errors?.comment && 'error'}
          {...register("phone", { required: true})}
          placeholder="+(38)(000)-00-00-000"
        />
      </label>
       {statusComment && <label className='box_input'>
         
        <textarea  type="textarea" className={errors?.comment && 'error'} {...register("comment", { required: true})} placeholder='.....' />
          </label>}
          <Button buttonClass='btn_form' text='  '/>
    </form>
  );
}
